<template>
  <v-container>
    <v-alert border="left" close-text="Close Alert" color="green accent-4" dark dismissible v-if="this.$route.params.message">
      {{ this.$route.params.message }}
    </v-alert>
    <v-row no-gutters>
      <v-col sm="4" class="pa-3" v-for = 'post in posts' :key='post.id'> 
        <v-card class="pa-1" :to="{ name: 'post', params:{id: post._id} }"> <!-- esari ni routing garna milxa , name of route is post and id required cha so passing tyo (watch route of Post) and route bata id ko value tanna milxa for del and update-->
          <v-img height="250" :src="`/${post.image}`"></v-img>
          <v-btn class = "ml-4 mt-3" small outlined color="indigo">
            {{post.category}}
          </v-btn>
          <v-card-title class= 'headline'>
            {{post.title}}
          </v-card-title>
          <v-card-text class="py-0">
            <p>{{post.content.substring(0,100) + "..."}}</p>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import API from '../api';

  export default {
    name: 'Home',
    data(){
      return{
        posts:[]
      }
    },
    async created(){      //read about this
      this.posts = await API.getAllPost()
    }
  }
</script>
